import React from 'react';
import { TextField, Button, Typography, Container, Box } from '@mui/material';
import { useFormik } from 'formik';
import * as Yup from 'yup';

const validationSchema = Yup.object({
  name: Yup.string().required('Nome é obrigatório'),
  cnpj: Yup.string().required('CNPJ é obrigatório'),
  cpf: Yup.string().required('CPF é obrigatório'),
  services: Yup.string().required('Serviços são obrigatórios'),
});

const CadastroOficina = () => {
  const formik = useFormik({
    initialValues: {
      name: '',
      cnpj: '',
      cpf: '',
      services: '',
      paymentMethods: '',
    },
    validationSchema: validationSchema,
    onSubmit: (values) => {
      console.log(values);
    },
  });

  return (
    <Container maxWidth="sm" style={{ padding: '40px 20px' }}>
      <Typography variant="h4" gutterBottom style={{ fontWeight: 'bold', color: '#003366', marginBottom: '30px' }}>
        Cadastro de Oficina
      </Typography>

      <form onSubmit={formik.handleSubmit}>
        <Box mb={3}>
          <TextField
            fullWidth
            label="Nome da Oficina"
            variant="outlined"
            name="name"
            value={formik.values.name}
            onChange={formik.handleChange}
            error={formik.touched.name && Boolean(formik.errors.name)}
            helperText={formik.touched.name && formik.errors.name}
          />
        </Box>
        <Box mb={3}>
          <TextField
            fullWidth
            label="CNPJ"
            variant="outlined"
            name="cnpj"
            value={formik.values.cnpj}
            onChange={formik.handleChange}
            error={formik.touched.cnpj && Boolean(formik.errors.cnpj)}
            helperText={formik.touched.cnpj && formik.errors.cnpj}
          />
        </Box>
        <Box mb={3}>
          <TextField
            fullWidth
            label="CPF do Responsável"
            variant="outlined"
            name="cpf"
            value={formik.values.cpf}
            onChange={formik.handleChange}
            error={formik.touched.cpf && Boolean(formik.errors.cpf)}
            helperText={formik.touched.cpf && formik.errors.cpf}
          />
        </Box>
        <Box mb={3}>
          <TextField
            fullWidth
            label="Serviços Oferecidos"
            variant="outlined"
            name="services"
            value={formik.values.services}
            onChange={formik.handleChange}
            error={formik.touched.services && Boolean(formik.errors.services)}
            helperText={formik.touched.services && formik.errors.services}
          />
        </Box>
        <Button variant="contained" color="primary" type="submit" size="large" fullWidth>
          Cadastrar Oficina
        </Button>
      </form>
    </Container>
  );
};

export default CadastroOficina;
